<template>
	<div class="empresa-planos" >
		<div class="card-planos" v-for="card in cards" :key="card.id">
			<div class="card-planos-titulo">
				<div>{{card.titulo}}</div>
				<span style="color: var(--verde); font-size: 1.5rem;">{{card.custo}} <span style="color: var(--c5); font-size: 0.6em;"> /mês </span> </span>
			</div>
			<div class="card-planos-itens">
				<div v-for="item in card.itens" :key="item"><va-icon name="done" color="azul" size="small"/> {{item}}</div>
			</div>
		</div>
	</div>
</template>

<script>
//import {url} from '../../configs/global'
export default {
	name: 'EmpresaPlanos',
	data: () => ({
		empresa: {},
		file: [],
		show_upload: false, 
		show : [false, false, false, false, false, false, false],
		valueInicio: null,
		valueFim: null,
		cards: [
			{	id: 3,
				titulo: 'Bronze',
				custo: 'R$ 200,00',
				itens: ['5 usuários', 'cadastro de até 70 ativos', 'cadastro de serviços', 'cadastro de reports', 'cadastro de documentos' , 'agendas' ]
			},
			{	id: 2,
				titulo: 'Prata',
				custo: 'R$ 250,00',
				itens: ['plano Bronze +', '20 usuários', 'cadastro de até 200 ativos', 'anexo de fotos', 'acesso às coordenadas dos ativos', 'cadastro de inspeções']
			},
			{	id: 1,
				titulo: 'Ouro',
				custo: 'R$ 300,00',
				itens: ['plano Prata +', '50 usuários', 'cadastro de até 1000 ativos', 'relatórios', 'dashboards de gerenciamento', 'abertura de chamados', 'usuários ' ]
			},
		]
    }),
    computed: {
        getEmpresa(){
			return this.$store.getters['empresa.get']
		}
    },
	watch: {
		getEmpresa(value){
			this.empresa = value
		}
	},
	methods:{
		load(){
			this.empresa = this.getEmpresa
		},
	},
	mounted() {
		this.load()
	},
}
</script>

<style scoped>

.empresa-planos{
	width: 100%;
	height: 260px;
	display: flex;
	flex-direction: row;
}

.card-planos{
	width: 30%;
	height: 100%;
	margin: 0px 10px;
	background-color: var(--cinza);
	border-radius: 10px;
}
.card-planos{
	width: 30%;
	height: 100%;
	margin: 0px 10px;
	background-color: var(--cinza);
	border-radius: 10px;
}
.card-planos-titulo{
	height: 70px;
	padding: 10px ;
	border-bottom: 1px solid var(--c3);
	color: var(--c3);
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.card-planos-itens{
	color: var(--c3);
	padding: 10px;
}

</style>