import { url } from '@/configs/global'

const format = (date) => {
    const year = date.getFullYear()
    const month = date.getMonth()+1
    const day = date.getDate()
    return `${year}-${month < 10 ? '0' : ''}${month}-${day < 10 ? '0' : ''}${day}`
}

const get_filtro = ({getters, }) => {
    if (!getters) return ""
    const filtros = getters['abas.getFiltro']
    const filtrosDash = filtros && filtros['dashboard'] ? filtros['dashboard'] : {}
    let dtStart = filtrosDash['dtStart']
    let dtEnd   = filtrosDash['dtEnd'];
    if (dtStart)  dtStart = format(new Date(dtStart))
    if (dtEnd)    dtEnd   = format(new Date(dtEnd))
    if (dtStart && dtEnd) return `?dtStart=${dtStart}&dtEnd=${dtEnd}`
    return ""
}

export default {
    state: () => ({
        resumo: {}
    }),
    mutations: {
        'dashboard.setResumo': function(state, {chave, valor}){
            state.resumo[chave] = valor
        },
    },
    getters: {
        'dashboard.getResumo': function(state){
            return state.resumo
        }
    },
    actions: {
        'dashboard.loadResumo': async function( { dispatch, getters, commit}, rota){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
            try {
                const response = await this.axios.get(`${url}/api/${id_e}/${rota}${get_filtro({getters})}`)
                commit('dashboard.setResumo', {
                    chave: rota,
                    valor: response.data.data
                })
            } catch (error) {
                dispatch('toast.error', error)
            }
            dispatch('loading.hide')
        }
    }
}